import { memo, useState } from "react";

import Liq from "./MainShopping/Liq";
import Acc from "./MainShopping/Acc";
import MyDev from "./MyDev";

const MyAndo = () => {
  const [acc, setAcc] = useState(false);
  const [liq, setLiq] = useState(false);
  const [myD, setD] = useState(false);

  // let manu; // .   DB    
  // let dev; // .   DB    
  let manu = ""; // .   DB    
  let dev = " Q"; // .   DB    
  let manu_text = manu ?? "";
  let dev_text = dev ?? "";
  let acc_list = new Array(30).fill(0); // 
  let liq_list = new Array(30).fill(0); // 

  const handleClick = (type, bool) => {
    if (type === "acc") setAcc(bool);
    else setLiq(bool);

    if (!dev) setD(bool);
  };

  //  dev      
  return (
    <div className="h-[440px] relative overflow-hidden">
      {!dev ? (
        <div
          className={`absolute z-10 bg-[#F5F4F1]/50 backdrop-blur-[2px] mt-[170px] w-[248px] h-[75px] rounded-[17px] mx-[56px] ando-animation${
            acc || liq ? " opacity-0" : " opacity-100"
          }`}
        >
          <p className="pt-2 text-14px text-[#5B5845] font-medium">
             .
          </p>
          <p className="rounded-2xl bg-[#989384] w-min text-white text-14px font-medium py-1.5 px-5 mt-1 mx-auto">
            &nbsp;
          </p>
        </div>
      ) : null}

      <MyDev dev_text={dev_text} manu_text={manu_text} dev={dev} myD={myD} />

      <div className="mt-6 flex w-min mx-auto gap-3">
        <div
          className="w-[100px] rounded-full h-[100px] border border-solid border-[#F2F2F2] flex flex-col justify-center"
          onClick={() => {
            handleClick("acc", true);
          }}
        >
          <img
            src="/imageDB/my_ando/acc.svg"
            width={40}
            className="mx-auto"
            alt=""
          />
          <p className="text-12px mt-[8px]"></p>
        </div>
        <div
          className="w-[100px] rounded-full h-[100px] border border-solid border-[#F2F2F2] flex flex-col justify-center"
          onClick={() => {
            handleClick("liq", true);
          }}
        >
          <span id="Liq" className="mx-auto" />
          <p className="text-12px"></p>
        </div>
      </div>
      <div
        className={`h-[217px] w-full bg-white acc relative${
          acc ? " go-up-acc" : ""
        }`}
      >
        {!dev ? (
          <div className="absolute z-10 bg-[rgba(68,68,68)]/30 h-[217px] bottom-0">
            <div className="bg-[#F5F4F1]/70 backdrop-blur-[2px] mt-[72px] w-[248px] h-[75px] rounded-[17px] mx-[56px]">
              <p className="pt-2 text-14px text-[#5B5845] font-medium">
                   .
              </p>
              <p className="rounded-2xl bg-[#989384] w-min text-white text-14px font-medium py-1.5 px-5 mt-1 mx-auto">
                &nbsp;
              </p>
            </div>
          </div>
        ) : null}

        <Acc
          acc_list={acc_list}
          dev_text={dev_text}
          handleClick={handleClick}
        />
      </div>
      <div
        className={`h-[267px] w-full bg-white liq${liq ? " go-up-liq" : ""}`}
      >
        {!dev ? (
          <div className="absolute z-10 bg-[rgba(68,68,68)]/30 h-[217px] bottom-0">
            <div className="bg-[#F5F4F1]/70 backdrop-blur-[2px] mt-[72px] w-[248px] h-[75px] rounded-[17px] mx-[56px]">
              <p className="pt-2 text-14px text-[#5B5845] font-medium">
                   .
              </p>
              <p className="rounded-2xl bg-[#989384] w-min text-white text-14px font-medium py-1.5 px-5 mt-1 mx-auto">
                &nbsp;
              </p>
            </div>
          </div>
        ) : null}

        <Liq
          liq_list={liq_list}
          dev_text={dev_text}
          handleClick={handleClick}
        />
      </div>
    </div>
  );
};

export default memo(MyAndo);
