// Swiper 
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper";
import "swiper/css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart } from "@fortawesome/free-solid-svg-icons";
import { faHeart as faHallowHeart } from "@fortawesome/free-regular-svg-icons";

const ph_image = "/imageDB/liq_trace/gangjuice.png";

export default function Curation() {
  let liqs = [
    {
      manuf: "",
      name: " ",
      img: "/imageDB/liq_trace/Novo-MSBlend.png",
      price: 20000,
    },
    {
      manuf: "",
      name: " ",
      img: "/imageDB/liq_trace/Storm-loyaloak.png",
      price: 20000,
    },
  ];
  for (let j = 0; j < 10; j++) {
    liqs.push({
      manuf: `${j}`,
      name: `${j}`,
      img: ph_image,
      price: 100,
    });
  }
  let icon = [""];
  let prefer = [""];
  let season = [""];
  let me_likey = [true, false];

  return (
    <>
      <div className="mx-4">
        <div className="mt-10 flex justify-between">
          <p className="font-medium">
            {icon}
            {prefer}  
          </p>
          <p className="text-[#A8A8A8] font-medium text-14px"></p>
        </div>
        <div className="mt-[4px] text-[#A8A8A8] text-left text-14px">
          {season}  {prefer} 
        </div>
      </div>
      <Swiper
        className="mySwiper mt-6"
        width={138}
        slidesOffsetBefore={16}
        slidesOffsetAfter={-16}
        spaceBetween={12}
        modules={[Pagination]}
      >
        {liqs.length > 0 ? (
          liqs.map((liq, i) => (
            <SwiperSlide
              key={"liq_" + i}
              className="min-h-[166px] curation-item no-drag flex flex-col"
            >
              <div className="bg-[#F5F5F5] w-full">
                <p className="w-full right-[7.87px] text-right absolute">
                  {me_likey[i] ? (
                    <FontAwesomeIcon
                      icon={faHeart}
                      className="text-[#FF0000]"
                    />
                  ) : (
                    <FontAwesomeIcon
                      icon={faHallowHeart}
                      className="text-[#FF0000]"
                    />
                  )}
                </p>
                <p className="my-3">
                  <img src={liqs[i].img} width={138} alt="" />
                </p>
              </div>
              <div className="mt-3 text-left tracking-[-0.28px]">
                <p className="text-14px font-medium">{liqs[i].manuf}</p>
                <p className="text-14px">{liqs[i].name}</p>
                <p className="font-bold">
                  {liqs[i].price.toLocaleString("ko-KR")}
                </p>
                <p className="mt-1 py-[1px] px-1 text-10px font-bold w-min bg-[#E6F0FF] text-[#0060F1]">
                  
                </p>
              </div>
            </SwiperSlide>
          ))
        ) : (
          <SwiperSlide className="min-h-[197px] mb-4 bg-[#F5F5F5] no-drag">
             !
          </SwiperSlide>
        )}
      </Swiper>
    </>
  );
}
