import { useState } from "react";

// Swiper 
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper";
import "swiper/css";

import AnimateHeight from "react-animate-height";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronUp,
  faChevronDown,
  faChevronRight,
  faPlus,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";

export default function Basket() {
  let parts = new Array(10).fill(0); // 
  // let parts = [];
  const price = 30000; // 
  const myPoint = 3000; // 

  const [b_on, setBasket] = useState(false);
  const toggleBasket = () => {
    setBasket((cur) => !cur);
  };

  const [start_x, setStart] = useState(0);
  const [x_coord, setX] = useState(0);
  const [slide, setSlide] = useState(0);

  const [pressed, setPress] = useState(false);

  return (
    // <div className="w-[360px] bg-[#F5F5F1]">
    <div className="w-screen max-w-sm bg-[#F5F5F1]">
      {
        <p
          className="font-medium py-4"
          onClick={() => {
            toggleBasket();
          }}
        >
           <FontAwesomeIcon icon={b_on ? faChevronUp : faChevronDown} />
        </p>
      }
      <AnimateHeight duration={500} height={b_on ? "auto" : 0}>
        <div
          onTouchStart={(e) => {
            setStart(e.touches[0].clientX);
            setPress(
              !(
                e.target.classList.contains("swiper-slide") ||
                e.target.classList.contains("swiper")
              )
            );
          }}
          onTouchMove={(e) => {
            if (pressed) {
              let sliding = x_coord + e.touches[0].clientX - start_x;
              if (sliding > 90) sliding = 90;
              else if (sliding < 0) sliding = 0;
              setSlide(sliding);
            }
          }}
          onTouchEnd={(e) => {
            if (pressed) setX(slide);
            setPress(false);
          }}
          style={{
            transition: "none",
            transform: `translateX(${slide}px)`,
          }}
        >
          <Swiper
            id="basket"
            width={102}
            slidesOffsetBefore={16}
            slidesOffsetAfter={-16}
            spaceBetween={10}
            modules={[Pagination]}
          >
            {parts.length > 0 ? (
              parts.map((part, i) => (
                <SwiperSlide key={i} className="basket-parts">
                  <div className="bg-white w-7 h-7 rounded-full absolute -top-2 -right-2 border-4 border-[#F5F5F5] flex justify-center items-center">
                    <FontAwesomeIcon
                      icon={faTimes}
                      className="text-[#B8B8B8]"
                    />
                  </div>
                  {i}
                </SwiperSlide>
              ))
            ) : (
              <SwiperSlide className="basket-parts flex flex-col">
                <p className="text-12px"> !</p>
                <p className="mt-4 mx-7 w-[48px] h-[48px] bg-[#F6F6F6] rounded-full flex justify-center items-center">
                  <FontAwesomeIcon icon={faPlus} />
                </p>
              </SwiperSlide>
            )}
          </Swiper>
          {parts.length > 0 ? (
            <>
              <div className="mx-[16px] flex justify-between">
                <p className="w-[126px] h-[26px] bg-[#5F5F5F] rounded-[5px]">
                  <span className="text-white text-14px">
                      
                  </span>
                </p>

                <div className="flex">
                  <div className="self-center">
                    <p className="w-[19px] h-[19px] bg-[#FFC700] rounded-full flex flex-col justify-center">
                      <img
                        src="img/Ap.png"
                        width={12.44}
                        className="mx-auto"
                        alt=" "
                      />
                    </p>
                  </div>
                  &nbsp;
                  <p className="font-bold text-12px self-center">
                    {myPoint.toLocaleString("ko-KR")}p
                  </p>
                  &nbsp;
                  <input
                    id="point-input"
                    className="w-[56px] h-[22px] rounded-[3px] self-center"
                  />
                  &nbsp;
                  <div className="text-12px self-center">
                    <span></span>
                  </div>
                </div>
              </div>
              <div className="text-right mt-[8px] mr-4 font-semibold">
                 {price.toLocaleString("ko-KR")}
              </div>
              <div className="pb-6 font-bold text-[#A1ADB7]">
                {/* <p className="w-[360px] slide-to-buy"> */}
                <p className="w-screen max-w-sm slide-to-buy">
                    {" "}
                  <FontAwesomeIcon
                    icon={faChevronRight}
                    className="text-14px"
                  />
                  <FontAwesomeIcon
                    icon={faChevronRight}
                    className="text-14px"
                  />
                </p>
              </div>
            </>
          ) : (
            <div className="mx-[16px] flex justify-between pb-[24px]">
              <p className="w-[126px] h-[26px] bg-[#5F5F5F] rounded-[5px]">
                <span className="text-white text-14px">  </span>
              </p>
            </div>
          )}
        </div>
      </AnimateHeight>
    </div>
  );
}
