import { faChevronLeft } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { memo } from "react";
import { useNavigate } from "react-router-dom";

const Header = () => {
  const href = window.location.href;
  const nav = useNavigate();

  return (
    <header className="py-4 flex items-center">
      {/*      .  "href.endsWith("/")" . */}
      {href.endsWith("landing") ? null : href.includes("landing") ? (
        <div
          className="ml-5 w-1/12"
          onClick={() => {
            nav(-1);
          }}
        >
          <FontAwesomeIcon icon={faChevronLeft} />
        </div>
      ) : href.endsWith("http://localhost:3000/") || href.endsWith("/") ? (
        <div
          className="grow ANDO-title"
          onClick={() => {
            nav("/");
          }}
        >
          ANDO
        </div>
      ) : href.includes("history") ? (
        <>
          <div
            className="ml-5 w-1/12"
            onClick={() => {
              nav(-1);
            }}
          >
            <FontAwesomeIcon icon={faChevronLeft} />
          </div>

          <div
            className="w-10/12 ANDO-title"
            onClick={() => {
              nav("/");
            }}
          >
             
          </div>
          <div className="mr-5 w-1/12"></div>
        </>
      ) : (
        <>
          <div
            className="ml-5 w-1/12"
            onClick={() => {
              nav(-1);
            }}
          >
            <FontAwesomeIcon icon={faChevronLeft} />
          </div>

          <div
            className="w-10/12 ANDO-title"
            onClick={() => {
              nav("/");
            }}
          >
            ANDO
          </div>
          <div className="mr-5 w-1/12"></div>
        </>
      )}
    </header>
  );
};

export default memo(Header);
